<template>
  <list-modal-form
    title=" "
    :items="items"
    :cols="cols"
    :deletable="item.id"
    @rowClick="setItem"
    @clearForm="clearForm"
    @submit="submit"
    @delete="deleteItem"
    ref="listModelForm"
  >
    <template #default>
      <v-row>
        <v-col cols="12" md="12">
          <v-text-field label=" * " v-model="item.name" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.shenasname_number" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.melli_code" />
        </v-col>
        <v-col cols="12" md="4">
          <date label=" " v-model="item.date_of_birth" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.father_name" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.phone" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.landline_phone" />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label=" " v-model="item.driving_licence_number" />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="  " v-model="item.health_card_number" />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="  " v-model="item.bank_card_number" />
        </v-col>
        <v-col cols="12" md="6">
          <v-text-field label="  " v-model="item.bank_account_number" />
        </v-col>
        <v-col cols="12" md="8">
          <v-text-field label=" " v-model="item.iban" />
        </v-col>
        <v-col cols="12" md="4">
          <v-text-field label=" " v-model="item.bank_name" />
        </v-col>
        <v-col cols="12">
          <v-textarea label="" v-model="item.explanation" />
        </v-col>
      </v-row>
    </template>
  </list-modal-form>
</template>
<script>
import ListModalFormMixin from "@/components/mcomponents/form/ListModalForm.js";
import GetApi from "./GetApi";

export default {
  mixins: [ListModalFormMixin, GetApi],
  data() {
    return {
      item: {},
      itemTemplate: {},
      baseUrl: "dashtbashi/drivers",
      leadingSlash: true,
      cols: [
        {
          th: "",
          td: "name",
          type: "text",
          filters: ["name"]
        }
      ]
    };
  },
  computed: {
    items() {
      return this.$store.state.drivers;
    }
  },
  methods: {
    getData() {
      this.getDrivers(true);
    },
    getSerialized() {
      let item = this.extractIds(this.item);
      return item;
    }
  }
};
</script>